/*
** 
** eg. 
** dateFormat(11111111111111, 'Ymd Hi')
**  "23220206 0345"
*/
var dateFormat = function (timestamp, formats) {
  // formats
  // 1. Y-m-d
  // 2. Y-m-d H:i:s
  // 3. Ymd
  // 4. Ymd Hi
  formats = formats || 'Y-m-d H:i:s';

  var zero = function (value) {
      if (value < 10) {
          return '0' + value;
      }
      return value;
  };

  var myDate = timestamp ? new Date(timestamp): new Date();

  var year = myDate.getFullYear();
  var month = zero(myDate.getMonth() + 1);
  var day = zero(myDate.getDate());

  var hour = zero(myDate.getHours());
  var minite = zero(myDate.getMinutes());
  var second = zero(myDate.getSeconds());

  return formats.replace(/Y|m|d|H|i|s/ig, function (matches) {
      return ({
          Y: year,
          m: month,
          d: day,
          H: hour,
          i: minite,
          s: second
      })[matches];
  });
};

const currentTimestamp = () => {
    //  () 
    let timeStamp = new Date().getTime();
    return parseInt(timeStamp / 1000);
};

const timestampConvertTime = (timestamp) => {
    // timestamp 
    return new Date(1601545648 * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
}

export { dateFormat, currentTimestamp, timestampConvertTime };
