const appendMetaHandle = (metaPropValue, metaContent, equivOrName) => {
    // h5 header  <meta> 
    // httpEquivOrName: 0=http-equiv, 1=name
    const currentProp = equivOrName === 0 ? 'httpEquiv' : 'name';
    if (typeof document !== undefined) {
        const metaTags = document.getElementsByTagName('meta');
        // console.log(metaTags);
        let isHasTag = false;
        for (let i = 0; i < metaTags.length; i++) { //meta
            const currentMetaObjAttributes = metaTags[i].attributes;
            for (let index = 0; index < currentMetaObjAttributes.length; index++) {
                const element = currentMetaObjAttributes[index];
                if (element.nodeValue === metaPropValue) {
                    isHasTag = true;
                }  
            }
        }
        if (!isHasTag) {
            const headItem = document.head;
            let oMeta = document.createElement('meta');
            oMeta.setAttribute(currentProp, metaPropValue);    // http-equiv Content-Security-Policy
            oMeta.content = metaContent;    // upgrade-insecure-requests
            headItem.appendChild(oMeta);
        }
    }
}

const appendMetaContentSecurityPolicy = () => {
    // HTTPSHTTPblock, head  meta
    // <meta http-equiv="Content-Security-Policy" content="upgrade-insecure-requests">
    //headmetahttp
    appendMetaHandle('Content-Security-Policy', 'upgrade-insecure-requests', 0);
};

const appendMetaReferrer = () => {
    // referer-policy content :
    // never http head  referer
    // default https  http  http header  referer 
    // origin origin ;
    // alwayshttp header  referer  https  http  referer
    appendMetaHandle('referrer', 'origin', 1);
};

export {
    appendMetaContentSecurityPolicy,
    appendMetaReferrer,
};